.typing {
  animation: typing 0.5s ease;
}
.typing h1 {
  color: #fff !important;
  font-size: 5rem;
}
.typing h1 span {
  font-size: 5rem;
  font-weight: bold;
  font-style: italic;
  background: linear-gradient(to left, #E67E22, #F4D03F);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@keyframes typing {
  0% {
    opacity: 0;
    transform: translateY(50px) scale(0.9);
  }
  100% {
    opacity: 1;
    transform: translateY(0px) scale(1);
  }
}
@media (max-width: 870px) {
  .typing h1 span {
    font-size: 3rem;
  }
}/*# sourceMappingURL=Typing.css.map */