.codes {
  display: grid;
  align-items: center;
  justify-items: end;
}
.codes ul {
  display: grid;
  grid-template-columns: 30px 30px;
  grid-gap: 15px;
}
.codes ul li {
  display: inline-block;
  list-style: none;
}
.codes ul li a {
  color: #fff;
  transition: 0.2s linear all;
}
.codes ul li a i {
  font-size: 1.5rem;
}
.codes ul li a:hover {
  color: #E67E22;
}/*# sourceMappingURL=Codes.css.map */